* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(90deg, #d1e2ee 0%, #ffd9eb 100%);
}

.App {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.title {
  padding: 25px 0px;
  font-size: 30px;
}

button.add-meal-btn, .add-ingredients-btn, .save-btn, .edit-btn, .delete-btn, .include-meal-btn, .exclude-meal-btn, .info-btn {
  border: none;
  padding: 15px;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 5px;
  color: rgb(250, 250, 250);
  background-color: hsl(180, 61%, 39%);
}

.add-meal-container {
  display: flex;
  gap: 10px;
  flex-direction: row;
  margin-bottom: 10px;
}
.add-meal-btn:hover {
  background-color: hsl(180, 61%, 29%);
}

.add-meal-btn:active {
  background-color: hsl(180, 61%, 24%);
}
.add-ingredients-btn {
  background-color: hsl(221, 100%, 50%);
}

.add-ingredients-btn:hover {
  background-color: hsl(221, 100%, 40%);
}

.add-ingredients-btn:active {
  background-color: hsl(221, 100%, 32%);
}

.add-ingredients-btn.modal-btn{
  margin: 0 auto;
}

.save-btn {
  width: 100%;
  background-color: hsl(221, 100%, 40%);
}

.save-btn:hover {
  background-color: hsl(221, 100%, 32%);
}


/* Modal Overlay */

.modal-overlay {
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  opacity: 0;
  pointer-events: none;
  transition: all 0.1s ease-in-out;
}

.modal-overlay.active {
  opacity: 1;
  pointer-events: all;
}

.modal-container {
  z-index: 999;
  position: fixed;
  top: 100px;
  left: 50%;
  transform: translate(-50%, 0);
  box-shadow: 0 4px 40px -8px rgba(0, 0, 0, 0.3);
  opacity: 0;
  transition: all 0.1s ease-out;
  pointer-events: none;
  width: 400px;
}

.modal-container.active {
  transform: translate(-50%,-50px);
  opacity: 1;
  pointer-events: all;
}

.modal-container .content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.close-popup {
  background: none;
  color: rgb(255, 255, 255);
  border: none;
  position: absolute;
  top: -15px;
  right: -37px;
  font-size: 50px;
  cursor: pointer;
}

.close-popup:hover{
  color: rgb(167, 3, 3);
}

/* Ingredients List */

form .form-element {
  display: flex;
  flex-direction: row;
  margin: 20px 0;
  gap: 5px;
  justify-content:  space-between;
  align-items: center;
}

ul.ingredient-list {
  max-height: 60vh;
  overflow-y:auto;
  max-width: 500px;
  width: 80%;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin:10px 0px;
  /* align-items: center; */
  font-size: 18px;
  gap: 5px;
}

li.ingredient-item, li.meal-item {
  min-height: 42.67px;
  width: 100%;
  padding: 0px;
  background-color: #fcfcfc;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  border-radius: 6px;
  overflow-y:hidden;
}

.ingredient-item span {
  padding-left: 10px;
  flex:1;
}

.meal-item span {
  padding-left: 10px;
}


.meal-item .meal-info {
  display: flex;
  align-items: center;
  flex: 1;
}


.meal-info button {
  color:#7e7e7e;
  font-size:16px;
  margin-left: 5px;
  width: 16px;
  height: 16px;
}

button.info-btn:hover {
  color:hsl(0, 0%, 29%);
}





.delete-btn, .edit-btn, .info-btn {
  padding: 0px 0px;
  border-radius: 0px;
  background-color: transparent;
  margin-right: 15px;
  /* font-size: 20px; */
}

.delete-btn {
  color: rgb(201, 25, 25);
}

.delete-btn:hover {
  color: hsl(0, 78%, 29%);

}

.edit-btn {
  color: hsl(221, 100%, 50%);
}

.edit-btn:hover {
  color: hsl(221, 100%, 30%);
}

.include-meal-btn {
  padding: 10px;
  border-radius: 0px;
  color: white;
}

.exclude-meal-btn {
  padding: 10px;
  border-radius: 0px;
  color: white;
  background-color: rgb(201, 25, 25);
}

.exclude-meal-btn:hover {
  background-color: hsl(0, 78%, 29%);
}





.include-meal-btn:hover {
  background-color: hsl(180, 61%, 29%);
}

/* Meal form  */

form.meal-form h3 {
  margin-bottom: 10px;
}


/* Search results */

.search-results-container {
  display: flex;
  gap: 7px;
  flex-wrap: wrap;
  margin-bottom: 15px;
}

.search-result {
  border: none;
  border-radius: 4px;
  padding: 10px;
  background: hsl(197, 68%, 82%);
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: 8px;
  font-size: 16px;
  font-weight: bold;
  /* cursor: pointer; */
  transition: all 0.15s ease-in-out;
}


.search-result.picked {
  cursor: pointer;
}

.search-result:hover {
  background: hsl(197, 68%, 77%);

}

.search-result .fa-circle-plus {
  color: hsl(118, 100%, 27%);
  /* font-size: 16px; */
}
.fa-circle-plus:hover {
  color: hsl(118, 100%, 35%);
}

.search-result .fa-pencil {
  color: hsl(221, 100%, 50%);
}

.search-result .fa-pencil:hover {
  color: hsl(221, 100%, 20%);

}

.search-result .fa-circle-minus {
  color: hsl(0, 98%, 25%);
  /* font-size: 16px; */
}

.search-result:hover .fa-circle-minus {
  color: hsl(0, 100%, 34%);
}


.search-result button {
  cursor: pointer;
  border: none;
  background-color: transparent;
  font-size: 16px;

}
/* Nutrient container */

.nutrients-container {
  margin-bottom: 5px;
}

.nutrients-container h3 {
  text-align: center;
}

.cards-container {
  display: flex;
  flex-direction: row;
  gap: 10px;
  margin: 10px 0px;
  /* flex-wrap: wrap; */
  justify-content: space-around;
  flex-wrap: wrap;
}

.cards-container span {
  padding: 10px;
  background-color: hsl(180, 61%, 39%);
  font-size: 20px;
  color: whitesmoke;
  border-radius: 10px;
  text-align:center;
}

/* Meal info  */

.meal-nutrients {
  margin: 10px 0px 0px 0px;
}

.meal-nutrients h3 {
  margin-top: 10px;
  margin-bottom: 15px;
}
.meal-nutrients span, .meal-ingredients span {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  min-width: 75px;
  text-align: center;
}

