  width: 44px;
} */
/* h5.amount-desc {
  width: 45px;
} */

.hidden {
  display: none;
  opacity: 0;
  pointer-events: none;
}


/* Floating action button */

.fab-overlay {
  background-color: rgb(255 255 255 / 90%);
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  opacity: 0;
  pointer-events: none;
  transition: all 0.1s ease-in-out;
}


.fab-overlay.active {
  opacity: 1;
  pointer-events: all;
}

.fab-container {
  position: fixed;
  top: 20px;
  left: 20px;

  /* cursor: pointer; */
}

.fab-icon-holder, .default-fab {
  height: 50px;
  width: 50px;
  background-color: hsl(221, 100%, 50%);
  border-radius: 100%;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
  cursor: pointer;

}

/* .fab-icon-holder:hover {
  opacity: 0.7;
} */


.fab-icon-holder i, .default-fab i {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 20px;
  color: whitesmoke;
}

.default-fab {
  width: 60px;
  height: 60px;
  background-color: hsl(180, 61%, 39%);
}

.default-fab:hover {
  background-color: hsl(180, 61%, 29%);
}


.default-fab i {
  transition: all 0.2s ease-in-out;
  transform: rotate(0deg);

}

.fab-options, .view-fab-options  {
  list-style-type: none;
  margin: 0;
  top: 60px;
  position: absolute;
  opacity: 0;
  transition: all 0.3s ease;
  transform: scale(0);
  transform-origin: 15% top;
}

.fab-options {
  right: -150px;
}

.fab-options li:last-child {
  padding-bottom: 15px;
}
.fab-options li:first-child {
  padding-top: 10px;
}


.default-fab:hover + .fab-options, .fab-options:hover {
  opacity: 1;
  transform: scale(1);
}

.fab-options.inactive {
  opacity: 0;
  pointer-events: none;
  transform: scale(0);
}

.default-fab:hover i {
  transform: rotate(180deg);
}

.view-fab-container .default-fab:hover i {
  transform: rotate(360deg);
}

.fab-container > .fab-options li {
  flex-direction: row;
  gap: 20px;
}

.fab-options li {
  display: flex;
  padding: 5px;
  flex-direction: row-reverse;
}

.fab-label {
  padding: 4px 10px;
  align-self: center;
  user-select: none;
  white-space: nowrap;
  font-size: 16px;
  background-color: hsl(0, 0%, 25%);;
  color: #ffffff;
  /* box-shadow:  0px 6px 20px rgba(0,0, 0, 0.2); */
  border-radius: 5px;
  height: fit-content;
  transition: all 0.2s ease-in-out;
  transform: translateX(-10px);
}

.fab-container .fab-icon-holder:hover + .fab-label {
  transform: translateX(-5px);
}

.fab-icon-holder:hover +  .fab-label {
  background-color: hsl(221, 100%, 30%);
  transform: translateX(-15px);
}


.fab-icon-holder:hover {
  background-color: hsl(221, 100%, 30%);
}

/* View FAB  */

.view-fab-container {
  position: fixed;
  top: 20px;
  right: 20px;
}

.view-fab-options {
  right: 0px;
  transform-origin: 85% top;
}


.eaten-title {
  margin: 10px auto;
  text-align: center;
}

.built-by {
  position: absolute;
  bottom: 10px;
}



@media (max-width: 500px)  {

  .modal-container {
    width: 85vw;
  }

  .close-popup {
    top: -5px;
    right: 9px;
    color: rgb(33, 33, 33);
    font-size: 45px;
  }

  form .form-element {
    margin: 14px 0px;
  }

  .form-element label {
    font-size: 14px;
  }

  .cards-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  h2.title {
    font-size: 25px;
  }
 
  /* View FAB */
  .view-fab-container {
    top: 10px;
    right: 10px;
  }

  .default-fab {
    width: 45px;
    height: 45px;
  }

  .fab-icon-holder i {
    font-size: 16px;
  }

  .default-fab i {
    font-size: 18px;
  }

  .fab-icon-holder {
    height: 38px;
    width: 38px;
  }

  .view-fab-options {
    top: 45px;
  }


  .fab-label {
    font-size: 14px;
  }

  /* add FAB */

  .fab-container {
    top: 10px;
    left: 10px;
  }
  
  .fab-container .fab-options {
    top: 45px;
    left: 0px;
  }

  .cards-container span {
    font-size: 16px;
  }

  .ingredient-item span {
    font-size: 16px;
  }





}
@media (max-width: 350px)  {

  .form-element > label {
    display: none;
  }

  h2.title {
    text-align: center;
    margin: 0px 60px;
  }


}

